import React, {Component} from 'react'

import {HomeIcon, GraphIcon, NotificationIcon, UsersIcon, LogoutIcon} from '../config/Icons'

class SidePanel extends Component {
    render(){
        const {switchActiveTab, activeTab, logOut, token} = this.props;



        return (
            <div className="side-panel">
                <ul>
                    <li className={activeTab === 'overview'? 'active' : ''} onClick={() => switchActiveTab('overview')}><HomeIcon /> <span>Overview</span></li>
                    {token.status === 10 && <li className={activeTab === 'graphs'? 'active' : ''} onClick={() => switchActiveTab('graphs')}> <GraphIcon /> <span>Graphs</span></li>}
                    {token.status === 10 && <li className={activeTab === 'userlogs'? 'active' : ''} onClick={() => switchActiveTab('userlogs')}> <UsersIcon /> <span>User Logs</span></li>}
                    {token.status === 10 && <li className={activeTab === 'notifications'? 'active' : ''} onClick={() => switchActiveTab('notifications')}> <NotificationIcon /> <span>Notifications</span></li>}
                    
                    
                    <li onClick={()=> logOut()}> <LogoutIcon /> <span>Logout</span></li>
                </ul>
            </div>
        )
    }
}

export default SidePanel